import React from 'react';
import styled from 'styled-components';
import { Link, useLocation } from 'react-router-dom'; // Import Link and useLocation from react-router-dom

const Nav = styled.nav`
  position: fixed;
  top: .5%;
  right: 5%;
  z-index: 10;
  mix-blend-mode: ${props => (props.isLanding ? 'difference' : 'normal')}; // Conditionally apply blend mode
`;

const NavList = styled.ul`
  display: flex;
  list-style-type: none;
  word-spacing: 20px;
  mix-blend-mode: ${props => (props.isLanding ? 'difference' : 'normal')};
`;

const NavItem = styled.li`
  margin-left: 20px;
  font-size: 1.2rem;
  mix-blend-mode: ${props => (props.isLanding ? 'difference' : 'normal')};

  &:not(:last-child)::after {
    content: '/';
    margin-left: 20px;
    color: rgba(255, 255, 255, 0.8);
  }
`;

const NavLink = styled(Link)`
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  position: relative;
  cursor: pointer;
  mix-blend-mode: ${props => (props.isLanding ? 'difference' : 'normal')};
  
  &::after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -5px;
    left: 0;
    background-color: #fff;
    transition: width 0.3s ease;
  }

  &:hover::after {
    width: 100%;
  }
`;

function Navigation() {
  const location = useLocation();
  const isLanding = location.pathname === '/'; // Check if we are on the landing page

  return (
    <Nav isLanding={isLanding}>
      <NavList isLanding={isLanding}>
        <NavItem isLanding={isLanding}><NavLink isLanding={isLanding} to="/">home *</NavLink></NavItem>
        <NavItem isLanding={isLanding}><NavLink isLanding={isLanding} to="/about">about *</NavLink></NavItem>
        <NavItem isLanding={isLanding}><NavLink isLanding={isLanding} to="/services">services *</NavLink></NavItem>
        <NavItem isLanding={isLanding}><NavLink isLanding={isLanding} to="/contact">contact *</NavLink></NavItem>
        <NavItem isLanding={isLanding}><NavLink isLanding={isLanding} to="/goal">goal *</NavLink></NavItem>
      </NavList>
    </Nav>
  );
}

export default Navigation;
