import React, { useState, useEffect, useRef } from 'react';
import styled, { keyframes } from 'styled-components';

const TerminalWrapper = styled.div`
  width: 325px;
  height: 220px;
  background-color: #141414;
  color: white;
  padding: 10px;
  border-radius: 20px;
  position: relative;
  z-index: 100;
  margin-top: 520px;
  margin-left: 770px;
`;

const TerminalHeader = styled.div`
  background: #c7c7c7;
  height: 30px;
  border-radius: 10px 10px 0 0;
  padding-left: 10px;
  display: flex;
  align-items: center;
`;

const TerminalButton = styled.div`
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: ${props => props.color};
  margin-right: 5px;
`;

const TerminalContent = styled.div`
  font-family: Menlo, Monaco, "Consolas", "Courier New", "Courier";
  font-size: 12px;
  padding: 20px;
`;

const TerminalLine = styled.div`
  display: flex;
  align-items: center;
`;

const Prompt = styled.span`
  margin-right: 10px;
`;

const Cursor = styled.span`
  opacity: 1;
  animation: blink 0.7s infinite;

  @keyframes blink {
    0%, 100% { opacity: 1; }
    50% { opacity: 0; }
  }
`;

const terminalText = [
  "Accessing Goal Data...",
  "Initializing Goal Setup...",
  "Download Successful",
  "Running Goal Analysis..."
];

const Terminal = () => {
  const [textIndex, setTextIndex] = useState(0);
  const [displayedText, setDisplayedText] = useState('');
  const [currentChar, setCurrentChar] = useState(0);

  useEffect(() => {
    if (currentChar < terminalText[textIndex].length) {
      const timeout = setTimeout(() => {
        setDisplayedText(prev => prev + terminalText[textIndex][currentChar]);
        setCurrentChar(prev => prev + 1);
      }, 100);
      return () => clearTimeout(timeout);
    } else {
      const delay = setTimeout(() => {
        setTextIndex((prevIndex) => (prevIndex + 1) % terminalText.length);
        setDisplayedText('');
        setCurrentChar(0);
      }, 2000);
      return () => clearTimeout(delay);
    }
  }, [currentChar, textIndex]);

  return (
    <TerminalWrapper>
      <TerminalHeader>
        <TerminalButton color="#3BB662" />
        <TerminalButton color="#E5C30F" />
        <TerminalButton color="#E75448" />
      </TerminalHeader>
      <TerminalContent>
        <TerminalLine>
          <Prompt>$</Prompt>
          <span>{displayedText}</span>
          <Cursor>|</Cursor>
        </TerminalLine>
      </TerminalContent>
    </TerminalWrapper>
  );
};

export default Terminal;
