import React, { useRef, useEffect } from 'react';
import { useLoader } from '@react-three/fiber';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { useAnimations } from '@react-three/drei';
import * as THREE from 'three';

function Graph3DObject({ onLoad }) {
  const ref = useRef();
  const gltf = useLoader(GLTFLoader, '/graphObject.glb'); // Ensure the path is correct
  const { animations } = gltf;
  const { actions } = useAnimations(animations, ref);

  useEffect(() => {
    // Apply white color to all meshes in the GLTF model
    gltf.scene.traverse((node) => {
      if (node.isMesh) {
        node.material = new THREE.MeshStandardMaterial({
          color: new THREE.Color('white'),  // Set color to white
          roughness: 0.3,
          metalness: 0.1,
          ambientOcclusion: 0.1,
        });
      }
    });

    if (actions && actions[Object.keys(actions)[0]]) {
      actions[Object.keys(actions)[0]].play();
    }

    if (onLoad) onLoad();
  }, [gltf, actions, onLoad]);

  return <primitive object={gltf.scene} ref={ref} scale={0.005} position={[-2, -2, 0]} />;
}

export default Graph3DObject;
