import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';

const TextContainer = styled.div`
  font-family: 'Courier New', monospace;
  font-weight: 100;
  font-size: .8rem;
  color: #FFFFFF;
  letter-spacing: 0.1rem;
  line-height: 1.5;
`;

class TextScramble {
  constructor(el) {
    this.el = el;
    this.chars = "!<>-_\\/[]{}—=+*^?#________";
    this.update = this.update.bind(this);
  }

  setText(newText) {
    const oldText = this.el.innerText || '';
    const length = Math.max(oldText.length, newText.length);
    const promise = new Promise((resolve) => (this.resolve = resolve));
    this.queue = [];
    for (let i = 0; i < length; i++) {
      const from = oldText[i] || "";
      const to = newText[i] || "";
      const start = Math.floor(Math.random() * 40);
      const end = start + Math.floor(Math.random() * 40);
      this.queue.push({ from, to, start, end });
    }
    cancelAnimationFrame(this.frameRequest);
    this.frame = 0;
    this.update();
    return promise;
  }

  update() {
    let output = "";
    let complete = 0;
    for (let i = 0, n = this.queue.length; i < n; i++) {
      let { from, to, start, end, char } = this.queue[i];
      if (this.frame >= end) {
        complete++;
        output += to;
      } else if (this.frame >= start) {
        if (!char || Math.random() < 0.28) {
          char = this.randomChar();
          this.queue[i].char = char;
        }
        output += `<span class="dud">${char}</span>`;
      } else {
        output += from;
      }
    }
    this.el.innerHTML = output;
    if (complete === this.queue.length) {
      this.resolve();
    } else {
      this.frameRequest = requestAnimationFrame(this.update);
      this.frame++;
    }
  }

  randomChar() {
    return this.chars[Math.floor(Math.random() * this.chars.length)];
  }
}

const ScrambledText = () => {
  const elRefs = [useRef(null), useRef(null), useRef(null), useRef(null), useRef(null)];
  const [isReady, setIsReady] = useState(false);

  const lines = [
    "9126e 2106v 152f c01.6^3.4",
    "Code line 2 with random chars",
    "Code line 3 with some variation",
    "91_[_6{3.46_*5{_",
    "Another random line here"
  ];

  useEffect(() => {
    setIsReady(true);
  }, []);

  useEffect(() => {
    if (!isReady) return;

    const scramblers = elRefs.map((el) => new TextScramble(el.current));
    let counter = 0;

    const next = () => {
      scramblers.forEach((scrambler, i) => {
        scrambler.setText(lines[i]).then(() => {
          if (i === 4) {
            setTimeout(next, 3000); // Pause before re-scrambling
          }
        });
      });
      counter = (counter + 1) % lines.length;
    };

    next();
  }, [isReady, elRefs]);

  return (
    <TextContainer>
      {elRefs.map((elRef, i) => (
        <div key={i} ref={elRef} />
      ))}
    </TextContainer>
  );
};

export default ScrambledText;
