import React, { useRef, useEffect, useState } from 'react';
import { useFrame, useLoader } from '@react-three/fiber';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { Color } from 'three';  // Import Color from Three.js

function ThreeJsObject() {
  const meshRef = useRef();
  const [error, setError] = useState(null);

  // Load the GLTF model
  const gltf = useLoader(
    GLTFLoader, 
    '/object.gltf',  // Ensure your GLTF model is in the public folder
    undefined,
    (error) => {
      console.error('An error occurred loading the 3D object:', error);
      setError(error);
    }
  );

  // Check if GLTF loaded successfully
  useEffect(() => {
    if (gltf) {
      console.log('3D object loaded successfully');
      
      // Traverse the scene to find materials and update their color and opacity
      gltf.scene.traverse((child) => {
        if (child.isMesh) {
          const material = child.material;
          
          // Set color to white
          material.color = new Color(1, 1, 1);  // RGB value for white
          
          // Set opacity and enable transparency
          material.opacity = 0.8;
          material.transparent = true;
        }
      });
    }
  }, [gltf]);

  // Apply rotation to the object
  useFrame((state, delta) => {
    if (meshRef.current) {
      meshRef.current.rotation.y += delta * 0.5;
    }
  });

  // Error fallback: Display a red box if there's an error loading the 3D model
  if (error) {
    return <mesh><boxGeometry args={[1, 1, 1]} /><meshStandardMaterial color="red" /></mesh>;
  }

  // Return the 3D object if it loads successfully
  return gltf ? (
    <primitive 
      ref={meshRef}
      object={gltf.scene} 
      scale={[0.032, 0.032, 0.032]}  // Adjust the scale to your preference
      position={[0, 0, 0]}  // Adjust the position to your preference
    />
  ) : null;
}

export default ThreeJsObject;
