import React, { useRef, useEffect } from 'react';
import { useLoader, useFrame } from '@react-three/fiber';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { useAnimations } from '@react-three/drei';

function Main3DObject({ onLoad }) {
  const ref = useRef();
  const gltf = useLoader(GLTFLoader, '/TriangleObject1.glb'); // Ensure the path is correct
  const { animations } = gltf;
  const { actions } = useAnimations(animations, ref);

  // Rotation state
  const rotationSpeed = 0.01; // Set the rotation speed for the object

  useEffect(() => {
    if (actions && actions[Object.keys(actions)[0]]) {
      const action = actions[Object.keys(actions)[0]];
      action.play();
      action.timeScale = 0.8; // Slow down the animation by adjusting timeScale
    }

    if (onLoad) onLoad();
  }, [actions, onLoad]);

  // Apply rotation to the object in every frame
  useFrame(() => {
    if (ref.current) {}
  });

  return <primitive object={gltf.scene} ref={ref} scale={0.5} position={[0, 0, 0]} />;
}

export default Main3DObject;
